header {
  position: fixed;
  width: 100%;
  background: $black-1;
  padding: 0 20px;
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 0px;
  z-index: 500;
  margin: 0 auto;
  transition: transform .3s; }

header.hidden {
  transform: translateY(-100px); }

a {
  text-decoration: none;
  font-size: 1.2em;
  font-weight: bold; }

a,
a:visited {
  color: $white-4; }

a:hover {
  color: $white-1; }

